* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}

body {
  justify-content: center;
  align-items: center;
  min-height: 100vh;
  display: flex;
}

.calendar {
  flex-wrap: wrap;
  justify-content: left;
  gap: 1px;
  width: 726px;
  padding: 10px;
  display: flex;
}

.calendar__day {
  background-color: #eee;
  border: 1px solid #000;
  width: 100px;
  height: 100px;
  transition: all .5s;
  position: relative;
  top: 0;
}

.calendar__day:hover {
  cursor: pointer;
  background-color: #ffbfcb;
  top: -20px;
}

